<!-- Êî∂ËøêÁªüËÆ°-->
<template>
    <view class="container">
        <PageHeader title="ÂïÜÊà∑Êî∂ËøêÁªüËÆ°" @back="back" />

        <view class="menu">

            <view class="filter-container">
                <!-- Áä∂ÊÄÅÈÄâÊã©Âô®ÁªÑ‰ª∂ -->
                <StatusPicker v-model="selectedStatus" :options="statusOptions" @change="onStatusChange" />

                <!-- Êó∂Èó¥ËåÉÂõ¥ÈÄâÊã©Âô®ÁªÑ‰ª∂ -->
                <TimeRangePicker v-model="selectedTimeRange" @change="onTimeChange" />
            </view>
        </view>
        <!-- ÁªüËÆ°‰ø°ÊÅØ -->
        <view class="tjxx">
            <view class="tj-item" v-for="(item, index) in statisticsConfig" :key="index">
                <image :src="item.image" mode="aspectFill"></image>
                <view class="ljts">
                    <view class="number">{{ item.number() }}</view>
                    <view class="title">{{ item.title }}</view>
                </view>
            </view>
        </view>

        <!-- ÂÜÖÂÆπÂå∫Âüü -->
        <view class="content-wrapper">
            <scroll-view class="content" scroll-y>
                <!-- Êï∞ÊçÆÂàóË°® -->
                <view class="order-list" v-if="allOrderList.length > 0">
                    <view class="order-item" v-for="(item, index) in allOrderList" :key="index">
                        <view class="order-header">
                            <view class="shop-info">
                                <text class="shop-name">{{ item.merchantName }}</text>
                                <StatusTag :status="item.status" />
                            </view>

                        </view>
                        <view class="order-content">
                            <InfoDisplay :fields="getInfoFields(item)" />
                        </view>
                        <view class="order-footer">

                            <uni-button size="mini" class="btn-confirm" @tap="handleViewDetails(item)">
                                Êü•ÁúãËØ¶ÊÉÖ
                            </uni-button>
                        </view>
                    </view>

                    <!-- Âä†ËΩΩÊõ¥Â§öÁªÑ‰ª∂ - Âè™Âú®ÊúâÊï∞ÊçÆÊó∂ÊòæÁ§∫ -->
                    <view class="loadMore">
                        <uni-load-more :status="loadingStatus" :content-text="{
                            contentdown: '‰∏äÊãâÊòæÁ§∫Êõ¥Â§ö',
                            contentrefresh: 'Ê≠£Âú®Âä†ËΩΩ...',
                            contentnomore: 'Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü'
                        }"></uni-load-more>
                    </view>
                </view>

                <!-- ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ -->
                <view class="empty-state" v-else-if="loadingStatus !== 'loading'">
                    <view class="empty-icon">üìã</view>
                    <text class="empty-text">ÊöÇÊó†Êï∞ÊçÆ</text>
                    <text class="empty-desc">ÂΩìÂâçÂàÜÁ±ª‰∏ãÊöÇÊó∂Ê≤°ÊúâÁõ∏ÂÖ≥‰ø°ÊÅØ</text>
                </view>
            </scroll-view>
        </view>
    </view>
</template>
<script setup>
import {
    ref,
    computed,
    onMounted
} from 'vue';
import {
    onPullDownRefresh,
    onReachBottom
} from '@dcloudio/uni-app';

import {
    apiGetPlanStatisticsPage, apiGetPlanStatistics
} from '@/api/apis.js';

import { useUserStore } from '@/stores/user.js'
import TimeRangePicker from '@/components/TimeRangePicker/TimeRangePicker.vue'
import StatusPicker from '@/components/StatusPicker/StatusPicker.vue'
import StatusTag from '@/components/StatusTag/StatusTag.vue'
import InfoDisplay from '@/components/InfoDisplay/InfoDisplay.vue'
import PageHeader from '@/components/PageHeader/PageHeader.vue'


const userStore = useUserStore();

// ÂàÜÂà´ÂÆö‰πâÁªüËÆ°Êï∞ÊçÆ
const bucketCount = ref(0);
const totalWeight = ref(0);



// ÁªüËÆ°ÈÖçÁΩÆÔºàÂõ∫ÂÆö‰∏çÂèòÔºâ
const statisticsConfig = [
    {
        image: '/static/shd/tjleft.png',
        number: () => bucketCount.value + ' ‰∏™',
        title: 'ÂûÉÂúæÊ°∂Êï∞'
    },
    {
        image: '/static/shd/tjright.png',
        number: () => totalWeight.value + ' kg',
        title: 'ÊÄªÈáçÈáè'
    }
];



const handleViewDetails = (item) => {
    console.log('Êü•ÁúãËØ¶ÊÉÖÊåâÈíÆË¢´ÁÇπÂáª', item);
    uni.navigateTo({
        url: `/pages/merchant/shsyDetail?id=${item.id}&merchantId =${item.merchantId}`
    });
};

// ÁîüÊàê‰ø°ÊÅØÂ≠óÊÆµÈÖçÁΩÆ
const getInfoFields = (item) => {

    const status = item.status;

    // Áä∂ÊÄÅ‰∏∫ 0ÔºàËøõË°å‰∏≠ÔºâÊàñ 2ÔºàÊó†Ê≥ïÊî∂ËøêÔºâÊó∂ÊòæÁ§∫È¢Ñ‰º∞‰ø°ÊÅØ
    if (status === 0 || status === '0' || status === 2 || status === '2') {
        return [
            {
                key: 'appointmentTime',
                label: 'È¢Ñ‰º∞Êó∂Èó¥',
                value: item.appointmentTime
            },
            {
                key: 'estimateWeight',
                label: 'È¢Ñ‰º∞ÈáçÈáè',
                value: item.estimateWeight
            },
            {
                key: 'estimateBucketNum',
                label: 'È¢Ñ‰º∞Ê°∂Êï∞',
                value: item.estimateBucketNum
            },
            {
                key: 'registrationNumber',
                label: 'ËΩ¶ËæÜ‰ø°ÊÅØ',
                value: item.registrationNumber
            },
            {
                key: 'address',
                label: 'Âú∞ÂùÄ',
                value: item.address
            }
        ];
    }

    // Áä∂ÊÄÅ‰∏∫ 1ÔºàÂ∑≤ÂÆåÊàêÔºâÊó∂ÊòæÁ§∫Êî∂Ëøê‰ø°ÊÅØ
    if (status === 1 || status === '1') {
        return [
            {
                key: 'arrivalTime',
                label: 'Êî∂ËøêÊó∂Èó¥',
                value: item.arrivalTime
            },
            {
                key: 'weight',
                label: 'Êî∂ËøêÈáçÈáè',
                value: item.weight
            },
            {
                key: 'bucketNum',
                label: 'Êî∂ËøêÊ°∂Êï∞',
                value: item.bucketNum
            },
            {
                key: 'registrationNumber',
                label: 'ËΩ¶ËæÜ‰ø°ÊÅØ',
                value: item.registrationNumber
            },
            {
                key: 'address',
                label: 'Âú∞ÂùÄ',
                value: item.address
            }
        ];
    }

    // ÈªòËÆ§ËøîÂõûÈ¢Ñ‰º∞‰ø°ÊÅØ
    return [
        {
            key: 'appointmentTime',
            label: 'È¢Ñ‰º∞Êó∂Èó¥',
            value: item.appointmentTime
        },
        {
            key: 'estimateWeight',
            label: 'È¢Ñ‰º∞ÈáçÈáè',
            value: item.estimateWeight
        },
        {
            key: 'estimateBucketNum',
            label: 'È¢Ñ‰º∞Ê°∂Êï∞',
            value: item.estimateBucketNum
        },
        {
            key: 'registrationNumber',
            label: 'ËΩ¶ËæÜ‰ø°ÊÅØ',
            value: item.registrationNumber
        }
    ];
};


//ÊêúÁ¥¢ÁªüËÆ°Êï∞ÊçÆ
const getToStatistics = async () => {
    const params = {
        merchantId: userStore.merchant?.id,
    };

    // Ê∑ªÂä†Á≠õÈÄâÊù°‰ª∂
    if (selectedStatus.value !== null) {
        params.status = selectedStatus.value;
    }

    if (selectedTimeRange.value && selectedTimeRange.value.length === 2 && selectedTimeRange.value[0] && selectedTimeRange.value[1]) {
        params.startTime = selectedTimeRange.value[0];
        params.endTime = selectedTimeRange.value[1];
    }
    // Ê∑ªÂä†ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
    const res = await apiGetPlanStatistics(params);

    if (res.code === 200) {
        bucketCount.value = res.data.bucketNum ?? 0;
        totalWeight.value = res.data.weight ?? 0;

    }
};


// Á≠õÈÄâÁõ∏ÂÖ≥Áä∂ÊÄÅ
const selectedStatus = ref(null); // ÈÄâ‰∏≠ÁöÑÁä∂ÊÄÅ
const selectedTimeRange = ref([]); // ÈÄâ‰∏≠ÁöÑÊó∂Èó¥ËåÉÂõ¥

// Áä∂ÊÄÅÈÄâÈ°πÈÖçÁΩÆ (0 ÂæÖÊî∂Ëøê 1 Â∑≤ÂÆåÊàê 2 Êó†ÈúÄÊî∂Ëøê)
const statusOptions = ref([
    { value: 0, text: 'ÂæÖÊî∂Ëøê' },
    { value: 1, text: 'Â∑≤ÂÆåÊàê' },
    { value: 2, text: 'Êó†ÈúÄÊî∂Ëøê' }
]);

// ËøîÂõû‰∏ä‰∏ÄÈ°µÊñπÊ≥ï
const back = () => {
    uni.navigateBack();
};

// Ê∑ªÂä†È°µÁ†ÅÂíåÂä†ËΩΩÁä∂ÊÄÅÂèòÈáè
const pageNum = ref(1);
const loadingStatus = ref('more'); // more-Âä†ËΩΩÂâç/loading-Âä†ËΩΩ‰∏≠/nomore-Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ

// Êï∞ÊçÆÂàóË°®
const allOrderList = ref([]);

//ÂêéËé∑ÂèñÊï∞ÊçÆ
const getNetwork = async () => {
    try {
        // Â¶ÇÊûú‰∏çÊòØÁ¨¨‰∏ÄÈ°µÔºåËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ‰∏∫Âä†ËΩΩ‰∏≠
        if (pageNum.value > 1) {
            loadingStatus.value = 'loading';
        }

        // ÊûÑÂª∫ËØ∑Ê±ÇÂèÇÊï∞
        const params = {
            pageNum: pageNum.value,
            merchantId: userStore.merchant?.id,
        };

        // Ê∑ªÂä†Á≠õÈÄâÊù°‰ª∂
        if (selectedStatus.value !== null) {
            params.status = selectedStatus.value;
        }

        if (selectedTimeRange.value && selectedTimeRange.value.length === 2 && selectedTimeRange.value[0] && selectedTimeRange.value[1]) {
            params.startTime = selectedTimeRange.value[0];
            params.endTime = selectedTimeRange.value[1];
        }

        const res = await apiGetPlanStatisticsPage(params);


        // Â§ÑÁêÜ‰∏ãÊãâÂà∑Êñ∞
        if (pageNum.value === 1) {
            allOrderList.value = res.data.list || [];
            uni.stopPullDownRefresh();
        } else {
            // Â§ÑÁêÜ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
            allOrderList.value = [...allOrderList.value, ...(res.data.list || [])];
        }

        // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
        if (res.data.list && res.data.list.length < 10) {
            // Â¶ÇÊûúËøîÂõûÁöÑÊï∞ÊçÆÂ∞ë‰∫éÊØèÈ°µÊï∞ÈáèÔºåËØ¥ÊòéÊ≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü
            loadingStatus.value = 'nomore';
        } else {
            // Âê¶ÂàôËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
            loadingStatus.value = 'more';
        }
    } catch (error) {
        console.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:', error);

        // ÂÅúÊ≠¢‰∏ãÊãâÂà∑Êñ∞
        uni.stopPullDownRefresh();

        // ÈáçÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        loadingStatus.value = 'more';

        // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÈ°µÂä†ËΩΩÂ§±Ë¥•ÔºåÁ°Æ‰øùÊòæÁ§∫ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ
        if (pageNum.value === 1) {
            allOrderList.value = [];
        }

        uni.showToast({
            title: 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
            icon: 'none',
            duration: 2000
        });
    }
};


// ‰∏äÊãâÂä†ËΩΩÊõ¥Â§öÊñπÊ≥ï
const onLoadMore = () => {
    if (loadingStatus.value === 'nomore') return;
    // È°µÁ†ÅÂ¢ûÂä†
    pageNum.value++;
    getNetwork();
};


// Ëß¶Â∫ïÂä†ËΩΩÊõ¥Â§ö
onReachBottom(() => {
    onLoadMore();
});

//‰∏ãÊãâÂà∑Êñ∞
onPullDownRefresh(() => {
    allOrderList.value = [];
    pageNum.value = 1; // ÈáçÁΩÆÈ°µÁ†Å‰∏∫1
    getNetwork();
    getToStatistics();
})

// Á≠õÈÄâÁõ∏ÂÖ≥ÊñπÊ≥ï

const onStatusChange = (value) => {
    selectedStatus.value = value;
    // ÈáçÁΩÆÈ°µÈù¢Âπ∂ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
    resetPageAndReload();
};


const onTimeChange = (value) => {
    console.log('Êó∂Èó¥ÂèòÂåñ:', value);
    selectedTimeRange.value = value;
    // ÈáçÁΩÆÈ°µÈù¢Âπ∂ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
    resetPageAndReload();
};


const resetPageAndReload = () => {
    allOrderList.value = [];
    pageNum.value = 1;
    getNetwork();
    getToStatistics();
};


// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÂàùÂßãÂåñÊï∞ÊçÆ
onMounted(() => {
    pageNum.value = 1;
    getToStatistics();
    getNetwork();
});
</script>

<style lang="scss" scoped>
.container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: $bg-theme-color;

    .menu {
        position: relative;
        margin-top: 2rpx;
        background-color: #ffffff;
        padding: 0 30rpx;

        .filter-container {
            display: flex;
            align-items: center; // ÂûÇÁõ¥Â±Ö‰∏≠
            justify-content: space-around; // Ê∞¥Âπ≥Âπ≥ÂàÜÁ©∫Èó¥
            height: 88rpx;

            .filter-item {
                display: flex;
                align-items: center;
                gap: 10rpx; // ÊñáÂ≠óÂíåÁÆ≠Â§¥Á¥ßÊå®ÁùÄ
                min-height: 60rpx;

                text {
                    font-size: 28rpx;
                    color: #333;
                }
            }

        }
    }

    .tjxx {
        display: flex;
        justify-content: space-between;
        height: 132rpx;

        margin: 30rpx;
        gap: 20rpx;

        .tj-item {
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: space-around;
            background: #fff;
            border-radius: 20rpx;

            image {
                width: 88rpx;
                height: 88rpx;
            }

            .ljts {
                .number {
                    font-size: 30rpx;
                    font-weight: bold;
                    color: #3D3D3D;
                }

                .title {
                    font-size: 24rpx;
                    color: #3D3D3D;
                }
            }
        }
    }

    .content-wrapper {
        flex: 1;

        .content {
            height: 100%;
        }

        .order-list {
            padding: 0 30rpx; // Â∑¶Âè≥30rpx

            .order-item {
                margin-bottom: 30rpx;
                padding: 30rpx;
                background-color: #fff;
                border-radius: 12rpx;

                .order-header {
                    margin-bottom: 20rpx;

                    .shop-info {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        margin-bottom: 16rpx;

                        .shop-name {
                            font-size: 28rpx;
                            font-weight: 400;
                            color: rgba(61, 61, 61, 1);
                        }

                    }
                }

                .order-content {
                    // Ê†∑ÂºèÂ∑≤ÁßªÂà∞ InfoDisplay ÁªÑ‰ª∂‰∏≠
                }

                .order-footer {
                    display: flex;
                    justify-content: flex-end;
                    margin-top: 20rpx;

                    .btn-cancel {
                        margin-right: 20rpx;
                        color: rgba(61, 61, 61, 1);
                        background-color: #fff;
                        border: 1px solid rgba(196, 196, 196, 1);
                        font-size: 26rpx;
                        width: 144rpx;
                        height: 48rpx;
                        border-radius: 20rpx;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        box-sizing: border-box; // ‰ΩøÁî®border-boxÁõíÊ®°Âûã

                    }

                    .btn-confirm {
                        color: rgba(7, 193, 96, 1);
                        // background-color: rgba(7, 193, 96, 1);
                        border: 1rpx solid rgba(7, 193, 96, 1);
                        font-size: 26rpx;
                        width: 144rpx;
                        height: 48rpx;
                        border-radius: 20rpx;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        box-sizing: border-box; // ‰ΩøÁî®border-boxÁõíÊ®°Âûã
                    }
                }
            }
        }
    }

    .loadMore {
        padding-bottom: calc(env(safe-area-inset-bottom) + 50);
    }

    // ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ
    .empty-state {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        min-height: calc(100vh - 200rpx); // Á°Æ‰øùÂç†Êª°Ââ©‰ΩôÂ±èÂπïÈ´òÂ∫¶
        padding: 120rpx 60rpx;
        text-align: center;
        background-color: #ffffff;

        .empty-icon {
            font-size: 120rpx;
            margin-bottom: 30rpx;
            opacity: 0.3;
        }

        .empty-text {
            font-size: 32rpx;
            color: rgba(61, 61, 61, 0.6);
            margin-bottom: 16rpx;
            font-weight: 500;
        }

        .empty-desc {
            font-size: 26rpx;
            color: rgba(61, 61, 61, 0.4);
            line-height: 1.5;
        }
    }

}
</style>